.burger-menu {
  position: relative;
  display: flex;
  width: 22px;
  height: 18px;
}

.burger-menu:before,
.burger-menu:after {
  content: '';
  position: absolute;
  background-color: #FFFFFF;
  height: 2px;
  width: 16px;
  left: calc(50% - 8px);
  transition: all 0.3s ease-out;
}

.burger-menu:before {
  top: 0;
}

.burger-menu:after {
  bottom: 0;
}

.burger-menu span {
  position: absolute;
  top: calc(50% - 1px);
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  transition: transform 0.1s ease-out;
}

.burger-menu.active:before {
  transform: rotate(45deg);
  top: 50%;
  width: 100%;
  left: 0;
}

.burger-menu.active:after {
  transform: rotate(-45deg);
  top: 50%;
  width: 100%;
  left: 0;
}

.burger-menu.active span {
  transform: scale(0);
}

.menu-main {
  position: fixed;
  right: -100%;
  width: calc(100% - 50px);
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  transition: right 0.3s ease-out;
  padding: 50px 20px 20px 20px;
  z-index: 5;
  overflow: auto;
  box-shadow: none;
}

.menu-main > ul .has-submenu ul {
  overflow: hidden;
  transition: max-height 0.3s ease-out;
  padding: 0 0 0 10px;
}

.menu-main > ul .has-submenu ul a {
  font-size: 20px;
  padding: 10px 0;
}

.menu-main > ul .has-submenu > a {
  user-select: none;
  display: flex;
  align-items: center;
}

.menu-main > ul .has-submenu > a:after {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 8px solid #ffffff;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  margin: 0 0 0 7px;
  transition: transform .3s ease-out;
  transform: rotate(0);
}

.menu-main > ul .has-submenu > a:hover {
  color: #ffffff;
}

.menu-main > ul .has-submenu.closed ul {
  margin: 0;
}

.menu-main > ul .has-submenu.closed > a:after {
  transform: rotate(90deg);
}

.menu-main > ul > li > a:hover {
  color: #7EB5F9;
}

.menu-main a {
  display: inline-block;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 15px 0;
}

.menu-main a.active {
  color: #7EB5F9;
}

.menu-main.active {
  right: 0;
}

