.form__input {
  display: flex;
  flex-direction: column;
  font-size: 0.66667rem;
  color: #707070;
  margin: 0 0 10px 0;
}

.form__input label {
  margin: 0 0 0 17px;
}

.form p {
  display: flex;
  align-items: center;
  position: relative;
}

.form p .check-icon {
  width: 20px;
  position: absolute;
  top: 1px;
  left: 3px;
  z-index: -1;
}

.form p .check-icon > path {
  fill: none;
  stroke: #3983C8;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 21px;
  stroke-dashoffset: 21px;
  transition: all 0.2s ease-out;
}

.form input[type='text'],
.form input[type='tel'] {
  height: 34px;
  width: 200px;
  border-radius: 17px;
  border: 1px solid rgba(57, 131, 200, 0.7);
  padding: 10px 20px;
}

.form input[type='text']::placeholder,
.form input[type='tel']::placeholder {
  color: #E2E2E2;
}

.form input[type='text']:focus,
.form input[type='tel']:focus {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.form input[type='text'].error,
.form input[type='tel'].error {
  border: 1px solid rgba(233, 23, 23, 0.85);
}

.form input[type='radio'] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form input[type='radio'] + label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  position: relative;
}

.form input[type='radio'] + label:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid #3983C8;
  border-radius: 50%;
  margin-right: 10px;
}

.form input[type='radio'] + label:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background-color: #3983C8;
  border-radius: 50%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-out;
}

.form input[type='radio']:checked + label {
  font-weight: 700;
}

.form input[type='radio']:checked + label:after {
  opacity: 1;
  visibility: visible;
}

.form input[type='checkbox'] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form input[type='checkbox'] + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  position: relative;
  cursor: pointer;
}

.form input[type='checkbox'] + label:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid #3983C8;
  border-radius: 50%;
  margin-right: 10px;
}

.form input[type='checkbox']:checked + label {
  font-weight: 700;
}

.form input[type='checkbox']:checked + label + .check-icon > path {
  stroke-dashoffset: 0 !important;
}

