.parallax-main {
  padding: 50px 0 55px 0;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.parallax-main__layer {
  position: absolute;
  top: -2%;
  right: 0;
  bottom: 0;
  left: -2%;
  transition: transform .1s ease-out;
}

.parallax-main__bg {
  background: linear-gradient(180deg, #4C79C3 0%, #7FA5E0 46.52%, #A3BEF4 100%);
}

.parallax-main__layer-1 {
  background: url("../img/main-parallax/layer-1.png") center top no-repeat;
  background-size: cover !important;
}

.parallax-main__layer-2 {
