  background-size: cover !important;
}

.parallax-main__layer-4 {
  background: url("../img/main-parallax/layer-4.png") center top no-repeat;
  background-size: cover !important;
}

.parallax-main__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 500px;
  padding: 40px 0;
  position: relative;
  margin: 0 0 55px 0;
}

.parallax-main__content:before {
  content: '';
  position: absolute;
  width: 300%;
  left: -100%;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.1);
  transition: opacity 1s ease-out;
  opacity: 0;
}

.parallax-main__content.active:before {
