  transform: translateX(0);
}

@keyframes arrow-animation-1 {
  from {
    transform: translateX(-50%) rotate(-45deg) translateY(-20px);
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(-20px) rotate(-45deg) translateY(0);
  }
}

@keyframes arrow-animation-2 {
  0% {
    transform: translateX(-50%) rotate(-45deg) translateY(-20px);
    bottom: 27px;
    opacity: 0;
  }

  50% {
    opacity: 0;
    bottom: 25px;
  }

  100% {
    opacity: 1;
    bottom: 23px;
    transform: translateX(-20px) rotate(-45deg) translateY(0);
  }
}

